/**
 * 灯光 - 环境光分量
 */
import {ShaderFloat} from './ShaderFloat'
import {D3Object} from '../../core/D3Object'

export class LightAmbientConstant extends  ShaderFloat{

    /**
     * @param light {L5.Light}
     */
    constructor(light) {
        super(1);
        this.allowUpdater = true;
        this.light = light;
    }

    /**
     * 更新环境光分量
     * @param visual {L5.Visual}
     * @param camera {L5.Camera}
     */
    update(visual, camera) {
        this.copy(this.light.ambient);
    }

    load(inStream) {
        super.load(inStream);
        this.light = inStream.readPointer();
    }

    link(inStream) {
        super.link(inStream);
        this.light = inStream.resolveLink(this.light);
    }

    save(outStream) {
        super.save(outStream);
        outStream.writePointer(this.light);
    }
};

D3Object.Register('L5.LightAmbientConstant', LightAmbientConstant.factory);
